<script>
import * as ProgressBar from 'progressbar.js';
import Chart from 'chart.js';

export default {
  name: "Index",
  data: () => ({
    videoBar: null,
    proceedsChart: null,
  }),
  mounted: function () {
    this.videoBar = new ProgressBar.SemiCircle('#viewed-videos-than-available', {
      strokeWidth: 6,
      color: '#FFEA82',
      trailColor: '#eee',
      trailWidth: 1,
      easing: 'easeInOut',
      duration: 1400,
      svgStyle: null,
      text: {
        value: '',
        alignToBottom: false
      },
      from: {color: '#f00'},
      to: {color: '#0f0'},
      // Set default step function for all animate calls
      step: (state, bar) => {
        bar.path.setAttribute('stroke', state.color);
        var value = Math.round(bar.value() * 100);
        if (value === 0) {
          bar.setText('');
        } else {
          bar.setText(value + '%');
        }

        bar.text.style.color = state.color;
      }
    });
    this.videoBar.text.style.fontFamily = '"Raleway", Helvetica, sans-serif';
    this.videoBar.text.style.fontSize = '2rem';

    this.videoBar.animate(0.73);


    //chart of proceeds
    Chart.defaults.global.defaultFontFamily = 'PIranSanas';
    this.proceedsChart = new Chart(this.$refs.proceedsChartElement, {
      type: 'line',
      data: {
        labels: ['1/1', '1/2', '1/3', '1/4', '1/5', '1/6', '1/7', '1/8', '1/9', '1/10', '1/11', '1/12', '1/13'],
        datasets: [{
          label: 'تعداد ویدئو دیده شده',
          data: [12, 34, 53, 22, 34, 22, 56, 44, 33, 21, 32, 34, 8],
          borderColor: 'lawnGreen',
        }]
      },
      options: {
        legend: {display: false, rtl: true},
        tooltips: {
          rtl: true
        }
      }
    });

  }
}
</script>

